import { Component, Host, Prop, h } from '@stencil/core';
import i18next from 'i18next';
import { billingState, widgetState } from '../../store';
import * as illus from './helper';

@Component({
  tag: 'charma-order-placed',
  shadow: true,
  styleUrl: 'charma-order-placed.scss',
})
export class CharmaOrderPlaced {
  // Passing this down so it will rerender once switched of language
  @Prop() language: string;
  @Prop() handleClose: () => void;

  svgArray = [illus.svg1, illus.svg3, illus.svg5, illus.svg6, illus.svg10];
  generateRandomNumber(): number {
    return Math.floor(Math.random() * (this.svgArray.length + 1)); // Generates a random number between 0 (inclusive) and 6 (exclusive)
  }

  render() {
    return (
      <Host>
        <section>
          <div class="rounded-md px-6 pb-6 mb-4 bg-white flex flex-col font-ns place-items-center text-center">
            <div class="pt-[100px] pb-[5px] ">{this.svgArray[this.generateRandomNumber()]}</div>
            <p class="text-xl font-medium mb-2 ">{i18next.t('thankyou.subtitle')}</p>
            <p class="mb-2" innerHTML={i18next.t('thankyou.order', { order: widgetState.order.shortId })}></p>
            <p
              class="mb-2"
              innerHTML={i18next.t('thankyou.desc', {
                email: billingState.email,
              })}
            ></p>
          </div>
        </section>
      </Host>
    );
  }
}
